.videoBackground{
    
    position: absolute;
  
    z-index: -1;
 
}
.videoBackground video{
  
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    opacity: 0.5;
    filter: contrast(200%);
}

.a{
    position: relative;
    z-index: 1;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
  
}
@keyframes rotate3d {
    from { transform: rotate3d(1, 0, 0, 0deg); opacity: 0;}
    to { transform: rotate3d(1, 0, 0, 360deg); opacity: 1; }
  }
@keyframes slidein {
    from { transform:translateX(100%);}
    to {  transform: translateX(0%);}
}
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  .n {
    font-size: larger;
    animation: rotate3d 1s 2s linear, fadeIn 2s ease-out;
    font-family: "Merriweather";
    font-size: 100px;

  }
  .b{
    animation-name: slidein;
    animation-duration: 2s;
    animation-timing-function:ease;
    animation-iteration-count:1;
    font-family: "Roboto", sans-serif;
  }
  .d{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
